import axios from 'axios'

const url = '/api/books/'

class BookService {
  static getBooks(type) {
    return axios
      .get(url, { params: { type } })
      .then((res) => {
        const data = res.data
        return data
      })
      .catch((err) => {
        throw err
      })
  }

  static insertBooks(key, value) {
    return axios.post(url, { key, value })
  }

  static deleteBooks(key) {
    return axios.delete(url, { data: { key } })
  }
}

export default BookService
