<template>
  <div class="container">
    <h1>Latest posts</h1>
    <div class="create-post">
      <label for="create-post">Add post</label>
      <input type="text" id="author" v-model="author" placeholder="author" />
      <div class="rate-selector-container">
        <div class="rate-selector" v-for="selectorIndex in 5" v-bind:key="selectorIndex">
          <img
            v-if="selectorIndex <= this.rate"
            v-on:click="selectRate(selectorIndex)"
            class="star-logo on star-selector"
            src="../assets/rate_star/rate_star.001.png"
          />
          <img
            v-else
            v-on:click="selectRate(selectorIndex)"
            class="star-logo off star-selector"
            src="../assets/rate_star/rate_star.002.png"
          />
        </div>
        <br />
      </div>
      <input type="text" id="text" v-model="text" placeholder="text" />
      <button v-on:click="createPost">Post!</button>
    </div>
    <p class="error" v-if="error">{{ error }}</p>
    <div class="post-container">
      <div
        class="post"
        v-for="(post, index) in posts"
        v-bind:item="post"
        v-bind:index="index"
        v-bind:key="post"
      >
        <p class="text">
          key: {{ index }} author: {{ JSON.parse(post).author }} date: {{ JSON.parse(post).date }}
          <br />
          text: {{ JSON.parse(post).text }}
        </p>
        <div class="rate-container">
          <template v-for="starIndex in 5" v-bind:key="starIndex">
            <img
              v-if="starIndex <= JSON.parse(post).rate"
              class="star-logo on"
              src="../assets/rate_star/rate_star.001.png"
            />
            <img v-else class="star-logo off" src="../assets/rate_star/rate_star.002.png" />
          </template>
        </div>

        <button v-on:click="deletePost(index)">Delete</button>
      </div>
    </div>
  </div>
</template>

<script>
import PostService from '../PostService'

function makeKey(length) {
  let result = 'POST:'
  const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789'
  const charactersLength = characters.length
  let counter = 0
  while (counter < length) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength))
    counter += 1
  }
  return result
}

export default {
  name: 'PostComponent',
  data() {
    return {
      posts: {},
      error: '',
      key: '',
      value: '',
      rate: 0,
    }
  },
  async created() {
    try {
      this.posts = await PostService.getPosts('POST:')
    } catch (err) {
      this.err = err.message
    }
  },
  methods: {
    async createPost() {
      var date = new Date()
      const fullDate = `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDay()}`
      var new_post = { author: this.author, date: fullDate, text: this.text, rate: this.rate }
      await PostService.insertPosts(makeKey(10), JSON.stringify(new_post))
      this.posts = await PostService.getPosts('POST:')
      this.author = ''
      this.text = ''
      this.rate = 0
    },
    async deletePost(key) {
      await PostService.deletePosts(key)
      this.posts = await PostService.getPosts('POST:')
    },
    async selectRate(rate) {
      this.rate = rate
    },
  },
}
</script>

