<template>
  <div class="container">
    <h1>Book Shelf</h1>
    <div class="create-book">
      <label for="create-book">Add book</label>
      <input type="text" id="author" v-model="author" placeholder="author" />
      <input type="text" id="text" v-model="title" placeholder="title" />
      <button v-on:click="createBook">Add book</button>
    </div>
    <p class="error" v-if="error">{{ error }}</p>
    <div class="book-list">
      <div v-for="(book, index) in books" :key="book.title" v-bind:index="index" class="book">
        <img :src="JSON.parse(book).cover" :alt="book.title" class="book-cover" />
        <div class="book-details">
          <h2>{{ JSON.parse(book).title }}</h2>
          <p>By {{ JSON.parse(book).author }}</p>
        </div>
        <button v-on:click="deleteBook(index)">Delete</button>
      </div>
    </div>
  </div>
</template>

<script>
import BookService from '../BookService'

function makeKey(length) {
  let result = 'BOOK:'
  const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789'
  const charactersLength = characters.length
  let counter = 0
  while (counter < length) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength))
    counter += 1
  }
  return result
}

export default {
  name: 'BookComponent',
  data() {
    return {  
      books: {},
      error: '',
    }
  },
  async created() {
    try {
      this.books = await BookService.getBooks('BOOK:')
    } catch (err) {
      this.err = err.message
    }
  },
  methods: {
    async createBook() {
      const templateBooKCover =
        'https://marketplace.canva.com/EAFjYY88pEE/1/0/1003w/canva-white%2C-green-and-yellow-minimalist-business-book-cover-cjr8n1BH2lY.jpg'
      var new_book = { author: this.author, title: this.title, cover: templateBooKCover }
      await BookService.insertBooks(makeKey(10), JSON.stringify(new_book))
      this.books = await BookService.getBooks('BOOK:')
      this.author = ''
      this.title = ''
    },
    async deleteBook(key) {
      await BookService.deleteBooks(key)
      this.books = await BookService.getBooks('BOOK:')
    },
  },
}
</script>

