import axios from 'axios'

const url = '/api/posts/'

class PostService {
  static getPosts(type) {
    return axios
      .get(url, { params: { type } })
      .then((res) => {
        const data = res.data
        return data
      })
      .catch((err) => {
        throw err
      })
  }

  static insertPosts(key, value) {
    return axios.post(url, { key, value })
  }

  static deletePosts(key) {
    return axios.delete(url, { data: { key } })
  }
}

export default PostService
